import FullWidthLayout from "hocs/layouts/FullWidthLayout";
import { connect } from "react-redux";
import axios from "axios";
import { useEffect, useState } from "react";
import { toast } from "react-toastify";
import { subcategoriaCreatetURL } from "constants";

function className(...classes) {
  return classes.filter(Boolean).join(" ");
}

function SubCategoriaCreate() {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const [loading, setLoading] = useState(false);

  const [formData, setFormData] = useState({
    categoria: "",
    descripcion: "",
    estado: true,
  });

  const { categoria, descripcion, estado } = formData;

  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();

    if (descripcion) {
      setLoading(true);

      const config = {
        headers: {
          "Content-Type": "application/json",
        },
      };
      
      const formData = new FormData();
      formData.append("categoria", categoria);
      formData.append("descripcion", descripcion);
      formData.append("estado", estado);
      const fetchData = async () => {
        axios
          .post(
            `${subcategoriaCreatetURL}`,
            formData,
            config
          )
          .then((res) => {
            setLoading(false);
            toast.success("Sub-Categoria Creada Correctamente");
          })
          .catch((err) => {
            setLoading(false);
            toast.error("Error al Crear la Sub-Categoria ");
          });
      };

      fetchData();
    } else {
      toast.error("Debes Ingresar los Datos Correctamente");
    }
  };

  return (
    <FullWidthLayout>
      <h2 className="text-2xl font-gilroy-black tracking-tight text-gray-900 sm:text-3xl dark:text-gray-400 ">
        Crear Sub-Categoria
      </h2>
      <form>
        <div class="relative z-0 mb-6 w-full group">
          <label
        
            class="block mb-2 text-sm font-medium text-gray-900 dark:text-gray-400"
          >
            Selecciona la Categoria
          </label>
          <select
            required
            class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500"
          >
            <option>--------</option>
            <option>Canada</option>
            <option>France</option>
            <option>Germany</option>
          </select>
        </div>
        <div class="relative z-0 mb-6 w-full group">
          <input
            type="text"
            value={descripcion}
            name="descripcion"
            onChange={(e) => onChange(e)}
            required
            class="block py-2.5 px-0 w-full text-sm text-gray-900 bg-transparent border-0 border-b-2 border-gray-300 appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer"
            placeholder=""
          ></input>
          <label
            for="name_subcategoria"
            class="peer-focus:font-medium absolute text-sm text-gray-500 dark:text-gray-400 duration-300 transform -translate-y-6 scale-75 top-3 -z-10 origin-[0] peer-focus:left-0 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-6"
          >
            Nombre de la Sub-Categoria
          </label>
        </div>
        <div class="flex items-start mb-6">
          <div class="flex items-center h-5">
            <input
              type="checkbox"
              value=""
              class="w-4 h-4 bg-gray-50 rounded border border-gray-300 focus:ring-3 focus:ring-blue-300 dark:bg-gray-700 dark:border-gray-600 dark:focus:ring-blue-600 dark:ring-offset-gray-800"
              required
            ></input>
          </div>
          <label
            for="remember"
            class="ml-2 text-sm font-medium text-gray-900 dark:text-gray-300"
          >
            Estado
          </label>
        </div>
        <div>
          {loading ? (
            <button className="float-right inline-flex justify-center py-3 px-6 border border-transparent shadow-sm text-base font-gilroy-medium rounded-full text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500">
              loading
            </button>
          ) : (
            <button
              type="submit"
              class="text-white bg-blue-700 hover:bg-blue-800 focus:ring-4 focus:outline-none focus:ring-blue-300 font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-blue-600 dark:hover:bg-blue-700 dark:focus:ring-blue-800"
            >
              Submit
            </button>
          )}
        </div>
      </form>
    </FullWidthLayout>
  );
}

const mapStateToProps = (state) => ({});

export default connect(mapStateToProps, {})(SubCategoriaCreate);
