import Error404 from 'containers/errors/Error404';
import Home from 'containers/pages/Home';
import store from './store';
import { Provider } from 'react-redux';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom'
//categoria
import CategoriaCreate from 'containers/pages/Inventario/Categoria/CategoriaCreate';
import CategoriaList from 'containers/pages/Inventario/Categoria/CategoriaList';
//subcategoria
import SubCategoriaCreate from 'containers/pages/Inventario/SubCategoria/SubCategoriaCreate';
import SubCategoriaList from 'containers/pages/Inventario/SubCategoria/SubCategoriaList';
//producto
import ProductoCreate from 'containers/pages/Inventario/Producto/ProductoCreate';
import ProductoList from 'containers/pages/Inventario/Producto/ProductoList';
//proveedor
import ProveedorCreate from 'containers/pages/Compra/Proveedor/ProveedorCreate';
import ProveedorList from 'containers/pages/Compra/Proveedor/ProveedorList';
//Cliente
import ClienteCreate from 'containers/pages/Facturacion/Cliente/ClienteCreate';
import ClienteList from 'containers/pages/Facturacion/Cliente/ClienteList';

function App() {
  return (
    <Provider store={store}>
      <Router>
        <Routes>
          {/* Error Display */}
          <Route path="*" element={<Error404/>}/>

          {/* Home Display */}
          <Route path="/" element={<Home/>}/>
        
          
          <Route path="/categoria-list" element={<CategoriaList/>}/>
          <Route path="/categoria-create" element={<CategoriaCreate/>}/>
          
          <Route path="/subcategoria-list" element={<SubCategoriaList/>}/>
          <Route path="/subcategoria-create" element={<SubCategoriaCreate/>}/>

          <Route path="/producto-list" element={<ProductoList/>}/>
          <Route path="/producto-create" element={<ProductoCreate/>}/>

          <Route path="/proveedor-list" element={<ProveedorList/>}/>
          <Route path="/proveedor-create" element={<ProveedorCreate/>}/>

          <Route path="/cliente-list" element={<ClienteList/>}/>
          <Route path="/cliente-create" element={<ClienteCreate/>}/>
        </Routes>
      </Router>
    </Provider>
  );
}

export default App;
